.footer__copyright {
  font-size: 11px;
  line-height: 2;
  margin-left: 45px;
}
@media (max-width: 767px) {
  .footer__copyright {
    margin: 32px 0 0;
  }
}
.footer__socials {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
@media (max-width: 767px) {
  .footer__socials {
    margin-top: 42px;
  }
}
.footer__social {
  display: block;
  margin: 0 15px;
}
.footer__social--vk {
  background: url("images/sprite-vk.svg");
}
.footer__social--fb {
  background: url("images/sprite-fb.svg");
}
.footer__social--inst {
  background: url("images/sprite-inst.svg");
}
.footer__callback {
  font-size: 13px;
  font-weight: bold;
  text-transform: uppercase;
  border: 0;
  background-color: transparent;
  padding: 0;
  margin: 0;
  cursor: pointer;
}
.footer__callback:before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  background: url("modules/footer/sprite-call.svg");
  margin-right: 30px;
}
